@use "../../assets/sass/mixins" as *;
@use "../../assets/sass/reset" as *;
@use "../../assets/sass/variables" as *;

.flip-card {
  background-color: transparent;
  width: 350px;
  height: 450px;
  perspective: 1000px;
  margin: 20px;
  border: 2px solid $primary-color-gold;

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
}

.flipped {
  transform: rotateY(180deg);
}

.flip-card-front {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  padding: 20px;
  height: 400px;
  width: 300px;
  margin: 20px;
  background-color: $primary-color-light-charcoal;
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    width: auto;
  }
}

.flip-card-back {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  padding: 20px;
  height: 350px;
  width: 300px;
  margin: 20px;
  background-color: $primary-color-light-charcoal;
  color: $primary-color-white;
  transform: rotateY(180deg);
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.project-name-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 15px;
  color: $primary-color-white;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 10px;

  &__view-project-button {
    background-color: $primary-color-gold;
    height: 40px;
    width: 50px;
    border-radius: 10px;
  }

  &__view-code-button {
    background-color: $primary-color-gold;
    height: 40px;
    width: 50px;
    border-radius: 10px;
  }
}

.coding-language-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
}

.logo {
  height: 50px;
}
