.about {
  height: auto;
  padding: 50px;
}

.about-section {
  display: flex;
  flex-wrap: wrap;
  &__header {
    color: $primary-color-white;
    padding-top: 50px;
  }

  &__headshot {
    padding-top: 20px;
    height: 300px;
    width: 200px;
  }

  &__about-text {
    color: $primary-color-white;
    padding-top: 20px;
    padding-left: 50px;
    padding-right: 30px;
  }
}

.underline {
  font-weight: 300;
  display: inline-block;
  padding-bottom: 5px;
  position: relative;
}
.underline:before {
  content: "";
  position: absolute;
  width: 50%;
  height: 1px;
  bottom: 0;
  left: 0%;
  border-bottom: 1px solid $primary-color-gold;
}
